<template>
  <div class="shousuo">
       <van-search  v-model="keywords" placeholder="请输入搜索关键词" shape="round" />
       <div class="zz"></div>
       <div class="qbs" @click="viewProduct(item.pid)" v-for="(item,index) in sousuo1()" :key="index">
           <div class="tp">
               <img :src="item.largeImg">
           </div>
           <div class="ssmz">{{item.name}}</div>
           <div class="ssyw">{{item.enname}}</div>
           <div class="ssprice">{{item.price}}</div>
       </div>
  </div>
</template>

<script>
export default {
  name: "Shousuo",
  data() {
    return {
        keywords:'',
        sera:[]
    };
  },

  computed: {
      Search(){
          if(this.keywords){
              return this.sera.filter((value)=>{
                  return value.name.includes(this.keywords)
              })
          }
      },
  },

  created() {
      this.shousuo();
  },

  methods: {

    shousuo(keywords) {
        
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: this.baseUrl + "/search",
        params: {
          appkey: this.appkey,
          name:this.keywords
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code == "Q001") {
            this.sera = res.data.result;
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    sousuo1(){
        if(!this.keywords){
            return this.sera;
        }
        return this.Search;
    },

    viewProduct(pid){
          this.$router.push({name:"Detail" , params:{pid}})
      }
  },
};
</script>

<style lang="less" scoped>
.shousuo{
    .zz{
        width: 100%;
        height: 120px;
        background:#f6c73d;
    }
    .qbs{
        width: 95%;
        height: 125px;
        background: white;
        position: relative;
        top: -25px;
        left: 9px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        margin-bottom: 15px;
        .tp{
            width: 100px;
            height: 100px;
            float: left;
            margin-top: 15px;
            margin-left: 15px;
            img{
                width: 100%;
                height: 100%;
                float: left;
            }
        }
        .ssmz{
            width: 120px;
            height: 35px;
            font-size: 16px;
            margin-left: 135px;
            padding-top: 35px;
            line-height: 35px;
        }
        .ssyw{
            width: 85px;
            height: 25px;
           font-size: 14px;
           white-space:nowrap;
           overflow: hidden;
           margin-left: 135px;
        }
        .ssprice{
            float:right;
            margin-right: 25px;
            font-size: 18px;
            color: #f6c942;
        }
    }
}
</style>